$(function () {
    $(document).on("click", "#lastBtn", function (e) {
        history.go(-1);
        e.preventDefault();
        return false;
    });

    $("#tmBlock").show();
});


angular.module("MyApp", ["FilterM"])
         .controller("MyController", ["$scope", "$http", "$filter", function ($scope, $http, $filter) {

             $scope.TM = {};
             $scope.VM = {};
             $scope.VM.Uname = "";
             $scope.VM.Oname = "";
             $scope.VM.fList = [];

             $scope.VM.fAry = fAry;
             $scope.VM.iAry = iAry;

             var i = 0;
             angular.forEach($scope.VM.fAry, function (item, index) {
                 var obj = {};
                 obj.F = item;
                 obj.I = $scope.VM.iAry[i];
                 $scope.VM.fList.push(obj);
                 i++;
             });

             $('#tmBlock').show();

             $scope.getCheck = function (record) {
                 var rtn = "";
                 $.each($scope.VM.checkAry, function (index, item) {
                     if (item.EV == record.C) {
                         rtn = item.EN;
                     }
                 });
                 return rtn;
             };

             $scope.goDelete = function (item, index) {

                 if ($scope.VM.fList.length <= 1) {
                     alert("最少需要一個附檔，所以無法刪除");
                     return;
                 }

                 var postData = {};
                 postData["i"] = item.I;
                 postData["ui"] = i;

                 $http({
                     method: 'POST',
                     url: "/System/AccountM/AccountCheck_Detail_DeleteDocFileOP.aspx",
                     data: $.param(postData),
                     headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
                 })
                 .success(function (data, status, headers, config) {
                     data.FileCount = data.FileCount || 0;
                     data.Chk = data.Chk || 0;
                     if (data.FileCount == 0) {
                         alert("無附檔，刪除失敗");
                     }
                     else if (data.FileCount == 1) {
                         alert("最少需要一個附檔，所以無法刪除");
                     }
                     else {
                         if (data.Chk > 0) {
                             alert("刪除成功");
                             $scope.VM.fList = $filter('filter')($scope.VM.fList, { I: '!' + item.I });
                             $scope.$apply(function () { });
                         }
                         else {
                             alert("刪除失敗");
                         }
                     }
                 })
                  .error(function (data, status, headers, config) {
                  });
             };
}]);

